<template>
    <div id="app">
        <div class="window_con">
            <div class="btn_close" @click="closeWindow">
                <img src="@/assets/icon_close@2x.png" />
            </div>
            <div class="userImg" v-show="!isPlay&step==0"
                :style="{width:canvasWidth+'px',height:canvasHeight+'px',marginLeft: -canvasWidth/2+'px'}"><img
                    :src="userImg" height="100%" /></div>
            <div class="userImg" v-if="originImg" v-show="isPlay||step!==0"
                :style="{width:canvasWidth+'px',height:canvasHeight+'px',marginLeft: -canvasWidth/2+'px'}"><img
                    :src="originImg+'?x-oss-process=image/resize,w_'+canvasHeight" height="100%" /></div>
            <canvas id="myCanvas" class="board" :width="canvasWidth" :height="canvasHeight"
                :style="{width:canvasWidth+'px',height:canvasHeight+'px',marginLeft: -canvasWidth/2+'px'}"></canvas>
            <div class="arrows" v-if="currStuInfo" v-show="currStuInfo.length>1">
                <div class="arrow_left" @click="checkPre" v-show="currPageIndex>0"><img src="@/assets/arrow_left.png" />
                </div>
                <div class="arrow_right" @click="checkNext" v-show="currPageIndex < currStuInfo.length-1"><img
                        src="@/assets/arrow_right.png" /></div>
            </div>
            <div class="menus" :style="{width:canvasWidth+'px',marginLeft: -(canvasWidth)/2+'px'}">
                <div @click="play" class="btnPlay">
                    <img v-if="isPlay" src="@/assets/pause.png" />
                    <img v-else src="@/assets/play.png" />
                </div>
                <progress-bar width="100%" @pbar-drag="setPercent" @pbar-seek="setPercent" :percent="percent">
                </progress-bar>
                <div class="speed"><span class="btn_speed" @click="setSpeed(1)"
                        :class="{active: speed==1}">4X</span><span class="btn_speed" @click="setSpeed(10)"
                        :class="{active: speed==10}">2X</span><span class="btn_speed" @click="setSpeed(20)"
                        :class="{active: speed==20}">1X</span></div>
            </div>
        </div>
    </div>
</template>
<script>
    import progressBar from '@/components/slider'
    import utils from '@/assets/utils.js';
    import token from '@/assets/token.js';
    import base64 from '@/assets/base64.js';
    import API from '@/assets/api.js'
    import axios from 'axios'
    var timer, n, len, x1, y1, w1, x2, y2, w2, tx, ty, tw;
    var gap = 2;
    var isStart = 0;
    var context = null;
    var ratioX = 1, ratioY = 1;
    var T9W_H = 74;
    var T10 = 75;
    var C5W = 60;
    var X9 = 71;


    export default {
        name: 'App',
        components: {
            'progressBar': progressBar
        },
        data() {
            return {
                deviceWidth: 29700,
                deviceHeight: 21000,
                C5W_WIDTH: 5535,
                C5W_HEIGHT: 10100,
                speed: 10,
                offset: 0,
                currStuInfo: null,
                currDeviceType: -1,
                currPageIndex: 0,
                step: 0,
                percent: 0,
                toStep: 0,
                userImg: "",
                originImg: "",
                trails: [],
                blocks: [],
                isPlay: false
            }
        },
        computed: {
            canvasHeight: function () {
                return window.innerHeight
            },
            canvasWidth: function () {
                return (this.canvasHeight) / (this.deviceWidth / this.deviceHeight)
            },
            gap: function () {
                return Math.ceil(100 / this.trails.length)
            }
        },
        filters: {
            formatDate(time) {
                let date = new Date(time);
                return formatDate(date, 'yyyy-MM-dd hh:mm');
            }
        },
        mounted() {
            //获取url参数
            let speed = localStorage.getItem("playSpeed")
            // console.log(speed)
            if(speed){
                this.speed = speed
            }
            console.log(this.canvasWidth, this.canvasHeight)
            this.currPageIndex = parseInt(utils.getUrlPrams("index"))
            this.currStuInfo = JSON.parse(localStorage.getItem('currStuInfo'));

            // console.log(this.currStuInfo)
            if (this.currStuInfo) {
                for (const item of this.currStuInfo) {
                    let image1 = new Image()
                    image1.src = this.imgUrl
                    let image2 = new Image()
                    image2.src = this.recogImgUrl
                }

            }
            this.initCanvas()
            this.initData()
        },
        methods: {
            initData() {
                this.userImg = this.currStuInfo[this.currPageIndex].recogImgUrl
                this.originImg = this.currStuInfo[this.currPageIndex].imgUrl
                this.locadBlocks()
                this.locadNoteInfo()
            },
            initCanvas() {
                let canvas = document.getElementById('myCanvas')
                context = canvas.getContext('2d')
                context.translate(0.5, 0.5);
                context.fillStyle = 'rgba(255, 255, 255, 0)';
            },
            closeWindow() {
                window.cefQuery({
                    request: "closeWindowPlayer",   // 代表请求方法 example事件名称 data 为传入参数
                    onsuccess: function (response) {
                        console.log("invoke success response = " + response);
                    },
                    onFailure: function (ecode, emsg) {
                        console.log("invoke fail code = " + ecode + " msg:" + emsg);
                    }
                });
            },
            setPercent(percent) {
                console.log(percent)
                isStart = 0
                this.isPlay = false
                context.clearRect(0, 0, this.canvasWidth, this.canvasHeight);
                this.toStep = parseInt(percent * this.trails.length / 100)
                this.step = 0
                this.speed = 0
                this.playTrails()
            },
            checkPre() {
                this.currPageIndex -= 1
                this.initData()
            },
            checkNext() {
                this.currPageIndex += 1
                this.initData()
            },
            setSpeed(speed) {
                this.speed = speed
                localStorage.setItem("playSpeed", speed)
            },
            play() {
                if(this.speed == 0){
                    this.speed = 10
                }else{

                }
                let block_key = this.currStuInfo[this.currPageIndex].blockKey
                if (block_key) {
                    this.isPlay = !this.isPlay
                    if (this.isPlay) {
                        this.playTrails()
                    }
                } else {
                    console.log("该页没有笔迹")
                }

            },
            playTrails() {
                // console.log(this.isPlay)
                let step = this.step
                let toStep = this.toStep

                // console.log(step)
                // console.log(toStep)
                if (step < toStep) {
                    this.readTrails(this.trails[step].color, this.trails[step].data, 0);
                } else {
                    if (step < this.trails.length && this.isPlay) {
                        this.readTrails(this.trails[step].color, this.trails[step].data, 0);
                    } else if (step == this.trails.length) {
                        this.step = 0
                        this.percent = 0
                        this.toStep = 0
                        this.isPlay = false
                        context.clearRect(0, 0, this.canvasWidth, this.canvasHeight);
                    }

                }

            },
            closeWindow() {
                window.cefQuery({
                    request: "closeWindowPlayer",   // 代表请求方法 example事件名称 data 为传入参数
                    onSuccess: function (response) {
                        console.log("invoke success response = " + response);
                    },
                    onFailure: function (ecode, emsg) {
                        console.log("invoke fail code = " + ecode + " msg:" + emsg);
                    }
                });
            },
            locadNoteInfo() {
                var that = this
                let note_key = this.currStuInfo[this.currPageIndex].noteKey
                let secret = API.api_trails_secret
                let source = API.api_trails_source
                console.log(source)
                let url = API.api_trails + 'notes/item_for_system'
                var post_data = {
                    source: source,
                    note_key: note_key,
                    e: Date.parse(new Date()) / 1000
                };
                // console.log(post_data)
                let value = token.getToken(url, secret, post_data)

                axios.post(API.api_trails + 'notes/item_for_system', {
                    source: source,
                    note_key: note_key,
                    e: Date.parse(new Date()) / 1000,
                    token: value,
                }).then(function (res) {
                    that.currDeviceType = res.data.data.device
                }).catch(function (error) {
                    console.log(error);
                });
            },
            locadBlocks() {
                var that = this
                let note_key = this.currStuInfo[this.currPageIndex].noteKey
                let block_key = this.currStuInfo[this.currPageIndex].blockKey
                let secret = API.api_trails_secret
                let source = API.api_trails_source
                let url = API.api_trails + 'trails/block'
                var post_data = {
                    source: source,
                    note_key: note_key,
                    block_key: block_key,
                    limit: 500,
                    offset: that.offset,
                    e: Date.parse(new Date()) / 1000
                };
                // console.log(post_data)
                let value = token.getToken(url, secret, post_data)

                axios.post(API.api_trails + 'trails/block', {
                    source: source,
                    note_key: note_key,
                    block_key: block_key,
                    limit: 500,
                    offset: that.offset,
                    e: Date.parse(new Date()) / 1000,
                    token: value,
                }).then(function (res) {
                    let arr = res.data.data
                    // console.log(arr)
                    that.offset += arr.length
                    if (arr != null) {
                        if (arr.length > 0) {
                            var block = [];
                            if (arr == null) arr = [];

                            arr.forEach(function (v) {
                                if (v.data) {
                                    v.data = new DataView(base64.decode(v.data.replace(/-/g, "+").replace(/_/g, "/")));
                                    block.push(v)
                                }
                            });

                            that.trails = block;

                            //如果没加载完那么继续加载
                            if (arr.length === 500) {
                                that.locadBlocks()
                            }
                        }
                    }
                    that.blocks = that.blocks.concat(arr)
                }).catch(function (error) {
                    console.log(error);
                });
            },
            drawTrails: function (c = -65536, x, y, w, s) {
                // console.log(x, y, w)
                var tx = ratioX * x;
                var ty = ratioY * y;
                var tw = w;
                let color = (c + 4294967296).toString(16).substr(-6)
                // console.log(color)
                if (c === 0) {
                    context.globalCompositeOperation = 'destination-out';
                    tw = 10 * w
                } else {
                    context.globalCompositeOperation = 'source-over';
                }
                context.setLineCap = "round"
                if (s == 16 || s == 0) {
                    if (this.isStart > 0) {
                        if (this.isStart > 1) {
                            context.lineTo(x1, y1)
                        } else {
                            context.lineTo(x1 + 1, y1 + 1)
                        }
                        context.setLineWidth(w1);
                        context.stroke()
                    }
                    isStart = 0;
                    return;
                }
                if (isStart > 0) {
                    x2 = ratioX * x;
                    y2 = ratioY * y;
                    // w2=tw;
                    var d = Math.sqrt(Math.pow(x1 - x2, 2) + Math.pow(y1 - y2, 2));

                    if (d > gap) {
                        tx = Math.round((x1 + x2) / 2 * 100) / 100;
                        ty = Math.round((y1 + y2) / 2 * 100) / 100;
                        if (isStart == 1) {
                            // console.log(tx,ty)
                            context.lineTo(tx, ty);

                        } else {
                            context.quadraticCurveTo(x1, y1, tx, ty);
                            context.lineWidth = tw;
                            context.stroke();
                            context.beginPath();
                            // timer = setTimeout(() => {
                            context.moveTo(tx, ty);
                            // }, 1000);
                        }
                        x1 = x2;
                        y1 = y2;
                    }
                    isStart++;
                } else {
                    /* 初始化画点*/
                    // isStart = 1;
                    context.beginPath();
                    context.lineCap = "round";
                    context.strokeStyle = "#" + color;//新增
                    x1 = tx;
                    y1 = ty;
                    context.moveTo(tx, ty);//起点
                    isStart++;
                }
            },
            readTrails: function (color, data, i = 0) {

                // console.log(i)
                // console.log(color)
                var that = this
                let currDeviceType = this.currDeviceType
                this.isDrawTrails = true;
                if (data.byteLength > i) {
                    var x, y, w;
                    if (currDeviceType === C5W) {
                        x = data.getFloat32(i, true) * this.canvasWidth / 10500;
                        y = (data.getFloat32(i + 4, true)) * (this.canvasHeight / 2.25) / 6350;
                        w = data.getFloat32(i + 8, true) * this.canvasWidth / 10500;
                    } else if (currDeviceType === T9W_H) {
                        x = (data.getFloat32(i, true)) * this.canvasWidth / 21000;
                        y = (data.getFloat32(i + 4, true)) * this.canvasHeight / 29700;
                        w = data.getFloat32(i + 8, true) * this.canvasWidth / 21000;
                    } else if (currDeviceType === T10) {
                        x = (data.getFloat32(i, true)) * this.canvasWidth / 21000;
                        y = (data.getFloat32(i + 4, true)) * this.canvasHeight / 29700;
                        w = data.getFloat32(i + 8, true) * this.canvasWidth / 21000;
                    } else if (currDeviceType === X9) {
                        x = (data.getFloat32(i, true)) * this.canvasWidth / 21000;
                        y = (data.getFloat32(i + 4, true)) * this.canvasHeight / 29700;
                        w = data.getFloat32(i + 8, true) * this.canvasWidth / 21000;
                    } else {
                        x = data.getFloat32(i, true) * this.canvasWidth / 21000;
                        y = data.getFloat32(i + 4, true) * this.canvasHeight / 29700;
                        w = data.getFloat32(i + 8, true) * this.canvasWidth / 21000;
                    }

                    this.drawTrails(color, x, y, w, 17)
                    if (this.speed) {
                        setTimeout(function () {
                            i += 20
                            // console.log(i)
                            that.readTrails(color, data, i)
                        }, this.speed)
                    } else {
                        i += 20
                        // console.log(i)
                        that.readTrails(color, data, i)
                    }

                } else {
                    this.drawTrails(color, 0, 0, 0, 0);
                    this.isDrawTrails = false;
                    this.step++
                    // console.log(this.speed)
                    if (this.speed) {
                        setTimeout(function () {
                            that.playTrails()
                        }, this.speed)
                    } else {
                        that.playTrails()
                    }

                }
                this.percent = this.step * 100 / this.trails.length

            },

        }
    }
</script>
<style>
    * {
        margin: 0;
        padding: 0;
    }

    body {
        background: transparent;
        display: flex;
        justify-content: space-around;
        align-items: center;
        overflow: hidden;
    }

    #app {
        font-family: "Helvetica Neue", Helvetica, "PingFang SC", "Hiragino Sans GB", "Microsoft YaHei", "微软雅黑", Arial, sans-serif;
        -webkit-font-smoothing: antialiased;
        -moz-osx-font-smoothing: grayscale;
        text-align: center;
        color: #333;
    }
</style>
<style scoped>
    #myCanvas {
        /* background: rgba(0, 0, 0, 0); */
        position: absolute;
        left: 50%;
        top: 0;
        z-index: 99;
    }

    .btn_close{
        padding: 0.2rem;
    }

    .userImg {
        position: absolute;
        left: 50%;
        top: 0;
        z-index: 1
    }

    .menus {
        height: 0.6rem;
        position: absolute;
        left: 50%;
        bottom: 0;
        z-index: 100;
        display: flex;
        justify-content: space-around;
        align-items: center;
        background: rgba(0, 0, 0, .6);
        padding: 0 0 0 0.3rem;
        box-sizing: border-box;
    }

    .arrows {
        width: 100%;
        position: absolute;
        left: 0;
        bottom: 48%;
        z-index: 100;
    }

    .arrows .arrow_left {
        float: left;
        cursor: pointer;
    }

    .arrows .arrow_right {
        float: right;
        cursor: pointer;
    }

    .speed .btn_speed {
        display: inline-block;
        cursor: pointer;
        font-size: 0.18rem;
        color: #fff;
        margin: 0 0.175rem;
    }

    .speed .btn_speed.active {
        color: #2C8DF0;
    }

    .btnPlay {
        display: inline-block;
        width: 0.25rem;
        height: 0.25rem;
        box-sizing: border-box;
        color: #fff;
        margin-right: 0.15rem;
    }

    .btnPlay img {
        width: 100%;
        height: 100%;
    }

    .btnClose {
        display: inline-block;
        width: 90px;
        border-radius: 6px;
        height: 30px;
        line-height: 30px;
        font-size: 14px;
        color: #fff;
        margin: 0 20px;
        background: rgba(73, 175, 79, 1);
    }
</style>